.card {
    width: 100%;
    overflow: hidden;
    padding: 25px;
  }
  
.card .nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  /* Image */
.card .img {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-top: 20px;
    position: relative;
  }

  .card .img img {
    width: 250px;
    height: 250px;
    object-fit: cover;
    border-radius: 50%;
    animation: rotate 20s linear;
    animation-iteration-count: infinite;
    animation-play-state: paused;
    user-select: none;
  }

  .card .img img.playing {
    animation-play-state: running;
  }

  @keyframes rotate {
    from { transform: rotate(0deg);}
    to { transform: rotate(360deg);}
  }

  .card canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 0;
    left: 0;
  }
